/**
 * Applies the correct theme color style to the specified property.
 * $property is typically color or background-color, but can be any CSS property that accepts color values.
 * $style should be one of the map keys in $theme-property-values (_variables.scss).
 */
.drawer-toggle .icon-pure-menu {
  color: rgba(255, 255, 255, 0.7);
  color: var(--theme-light-secondary, rgba(255, 255, 255, 0.7));
}

.drawer-toggle:hover .icon-pure-menu {
  color: #ffffff;
  color: var(--theme-light-primary, #ffffff);
}

