.footer-assist {
  padding-top: 4.5rem;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 1rem 0;
  border-top: 1px solid rgba(0, 0, 0, 0.12);
  color: rgba(0, 0, 0, 0.54);
}

.doc-journey {
  position: relative;
  padding: 2rem 15px;
}

.doc-journey h1 {
  line-height: 1;
}

.doc-journey .btn {
  width: 100%;
  padding: .75rem 1.5rem;
  margin-bottom: 1rem;
  border-radius: 5px;
  font-size: 1.125rem;
}

@media (min-width: 600px) {
  .doc-journey {
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
  }
}

@media (min-width: 1024px) {
  .doc-journey h1 {
    font-size: 3rem;
  }
  .doc-journey .btn {
    padding: .875rem 2rem;
    border-radius: 5px;
    font-size: 1.125rem;
  }
  .doc-journey .btn:first-child {
    margin-right: 2rem;
  }
}

@media (min-width: 1200px) {
  .doc-journey .btn {
    width: 40%;
  }
}
